body {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 50vh;
  font-family: 'Montserrat', sans-serif;
  font-size: medium;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  background-color: #5a8aa0;
  color: #ffffff;
}